* {
  box-sizing: border-box;
}

.links {
  cursor: pointer;
  user-select: none;
}

.infText {
  color: white;
  transform: translateX(-100%);
  animation: slideIn 0.7s ease-out forwards;
}

#ContactInformationComponent {
  background-color: rgb(214, 41, 70);
  display: flex;
  flex-direction: row;
  padding: 1rem;
  font-size: 1.5rem;
  width: 100%;
}

.notification {
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 15rem;
  height: 7rem;
  border-radius: 22px 22px;
  background-color: rgb(41, 145, 214);
}





@keyframes slideIn {
  from {
    transform: translateX(-100%);
  }

  to {
    transform: translateX(0);
  }
}